body p{
  font-size: 16px;
}


// -----------------------------  Navbar ---------------------------------
.navbar-link {
  font-size: 24px;
  margin-right: 1em;
}
.navbar-link:last-child {
  margin-right: 0;
}
#copy-text {
  font-family: Italic;
}
#nav-top {
  border-radius: 0;
  background-color: #fcd51b;
}
#nav-side {
  background-color: #fcd51b;
}
#nav-top .item {
  min-height: 4em;
}
#nav-top img {
  border-radius: 50%;
  height: 3em;
  width: 3em;
  margin: -.5em 0;
}
#nav-top img:first-child {
  border-radius: 0;
  margin: calc(-1em - 2px) calc(-1em - 1px) calc(-1em - 2px) calc(-1em - 2px) ;
  width: 4em;
  height: 4em;
}
footer {
  background-color: #fcd51b;
  padding-top: 1em;
  margin-top: 1em;
}

//---------------------------------- Cards --------------------------------------

.event-card {
  max-width: 100%;
  min-width: 100%;
}
.content {
  padding: 1rem;
}

